:root {
  box-sizing: border-box;
  --font-Roboto: 'Roboto', sans-serif;
  --font-Raleway: 'Raleway', sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-Roboto);
  margin: 0;
  background-color: ghostwhite;
}

h2,h3 {
  font-size: 2em;
  font-family: var(--font-Raleway);
  letter-spacing: 1px;
  font-weight: bold;
}


.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

input {
  width: 100%;
  font-size: 1.2rem;
  padding: .25em;
  color: #444444;
}

figure {
  margin: 0;
}

.titleCategory {
  text-align: center;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-style: italic;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 1em;
  margin: 1.5em 0;
}

.card {
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}
.card img {
  display: block;
  width: 100%;
  height: 170px;
  object-fit: cover;
}

.card p {
  padding: .75em;
  text-align: center;
  margin: 0;
  font-size: .8em;
  position: absolute;
  bottom: 0;
  background-color: #000000;
  color: #ffffff;
  left: 0;
  right: 0;
  opacity: .75;
  font-weight: bold;
  text-transform: capitalize;
}


